html,
body {
  height: 100%;
  width: 100%;
  overflow: hidden; }

body {
  font-family: 'SFProText';
  font-size: 16px;
  min-width: 320px;
  position: fixed; }

.container {
  margin: 0 auto;
  max-width: 1440px;
  padding: 0 20px;
  position: relative; }

.icon {
  fill: #000;
  stroke: #000; }

.nav-btn {
  width: 50px;
  height: 50px;
  padding: 15px 10px;
  margin: -15px -10px;
  display: flex;
  text-decoration: none; }
  .nav-btn__inner {
    position: relative;
    height: 100%;
    width: 100%; }
  .nav-btn__item {
    display: block;
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: #000;
    right: 0; }
    .nav-btn__item:nth-child(1) {
      top: 0px; }
    .nav-btn__item:nth-child(2) {
      top: 9px; }
    .nav-btn__item:nth-child(3) {
      bottom: 0px; }

.lazy {
  visibility: hidden;
  opacity: 0;
  transition: opacity 1s; }
  .lazy.visible {
    opacity: 1;
    visibility: visible; }

.cover-box {
  overflow: hidden;
  display: block;
  position: relative;
  background: rgba(0, 0, 0, 0.5); }
  .cover-box .lazy {
    display: block;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%); }
  .cover-box .wh {
    height: 100% !important; }
  .cover-box .ww {
    width: 100% !important; }
  .cover-box_contain {
    background-color: transparent; }
    .cover-box_contain .wh {
      width: 100% !important;
      height: auto !important; }
    .cover-box_contain .ww {
      height: 100% !important;
      width: auto !important; }
  .cover-box_size-auto {
    display: flex;
    align-items: center;
    background-color: transparent; }
    .cover-box_size-auto .lazy {
      position: static;
      left: 0;
      top: 0;
      width: 100% !important;
      height: auto !important;
      transform: translate(0); }

